import { createStore, applyMiddleware } from 'redux';
import { composeWithDevTools } from 'redux-devtools-extension';
import thunk from 'redux-thunk';
import rootReducer from './reducers';
import watch from 'redux-watch'
import {CHANGE_PRODUCT_SAVE_BUTTON_STATE} from "./actions/types/Products/MakeProductTypes";
import {changeProductSaveButtonState} from "./actions/products/makeProductActions";

const initialState = {};

const middleware = [thunk];

const store = createStore(
    rootReducer,
    initialState,
    composeWithDevTools(applyMiddleware(...middleware))
);

let w = watch(store.getState, 'product.makeProduct.data')
store.subscribe(w((newVal, oldVal, objectPath) => {
  store.dispatch(changeProductSaveButtonState(true))
}))

 
export default store;